
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import OrderItem from '@/components/OrderItem'
import {mapGetters} from 'vuex'
export default {
  head: {
    title: {
      inner: '我的订单'
    }
  },
  data () {
    return {
      pulldownConfig: {},
      pullupConfig: {
        content: '下拉刷新'
      },
      height: '0px',
      bar: [{
        text: '全部',
        key: 'all'
      }, {
        text: '待报价',
        key: 'price'
      }, {
        text: '待付款',
        key: 'pay'
      }, {
        text: '待出单',
        key: 'issue'
      }, {
        text: '已出单',
        key: 'issued'
      }],
      current: '全部',
      index: 0,
      getBarWidth: function (index) {
        console.log(index)
        return (index + 1) * 22 + 'px'
      }
    }
  },
  mounted () {
    console.log(document.querySelector('.vux-slider').clientHeight)
    this.height = document.querySelector('.vux-slider').clientHeight + 'px'
  },
  created () {
    console.log(this.$route.params.id)
    for (const i in this.bar) {
      if (this.bar[i].key === this.$route.params.id) {
        this.index = parseInt(i)
      }
    }
    // this.index = this.$route.params.id
  },
  computed: {
    ...mapGetters({
      list: 'getOrderList'
    })
  },
  components: {
    Tab,
    TabItem,
    Swiper,
    SwiperItem,
    Sticky,
    OrderItem,
    Scroller
  },
  methods: {
    handleChange (item, index) {
      this.index = index
      this.$router.push(this.bar[this.index].key)
    },
    handleSwiper (index) {
      console.log(this.bar[index])
      this.$router.push(this.bar[index].key)
    }
  }
}



